body {
  margin: 0;
  padding: 0;
}

/*----------------------------------------------------------------------------------*/
.first {
  background: linear-gradient(
    244.54deg,
    rgba(0, 153, 255, 0.42) 5.48%,
    rgba(110, 76, 255, 0.755) 62.63%,
    #db00ff 109.63%
  );
}
.Navbar {
  padding-top: 21px;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
}

.headi {
  margin-top: 95px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  padding-bottom: 52px;
}

.midibox {
  width: 400px;
  height: 120px;
  margin: 0 auto;
  background: linear-gradient(
    250.8deg,
    #ffffff 3.87%,
    #e1c2e7 31.78%,
    #9bd1dd 47.66%,
    #e8b4f0 63.06%,
    #ffffff 96.27%
  );
  border-radius: 15px;
}
@media only screen and (max-width: 425px) {
  .midibox {
    width: 300px;
    height: 92px;
  }
}
.headi p {
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
}

.cont p {
  margin-right: 40px;
  margin-left: 40px;
  font-size: 20px;
}

@media only screen and (max-width: 425px) {
  .cont p {
    font-size: 15px;
    margin: 0;
    text-align: justify;
    text-justify: inter-word;
  }
}
/*------------------------------------------------------------------*/

.beg {
  width: 90%;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
  font-weight: bolder;
  padding-left: 40px;
  padding-right: 10px;
}
.beg h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  margin: 0 auto;
}

@media only screen and (max-width: 500px) {
  .beg {
    padding-left: 10px;
  }
  .beg h1 {
    font-size: 22px;
  }
}

/*-------------------------------------------------------------------*/

.textboxes {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 23px;
  line-height: 22px;
  color: #3a3a3a;
  margin-bottom: 43px;
}

@media screen and (max-width: 500px) {
  .textboxes {
    font-size: 18px;
  }
}

/*---------------------------------------SECTORS-----------------*/

.fni i {
  color: rgba(255, 0, 184, 0.69);
}
.hc i {
  color: rgba(199, 39, 39, 0.81);
}
.cons i {
  color: #808080;
}
.edu i {
  color: #248fb0;
}
.ear i {
  color: #e4f51c;
}
.ent i {
  color: #24d2d2;
}
.transp i {
  color: #e67105;
}
